import * as React from "react";
import { Component } from "react";

import { connect } from "react-redux";

import HUD from "./HUD";
import Foreground from "./Foreground";

import { OpponentInterface } from "utils/types";

interface State {
  gameWidth: number;
  currentEnemy: OpponentInterface;
}

class Game extends Component<{}, State> {
  state = {
    gameWidth: 1420,
    currentEnemy: {
      hp: 100,
      name: "הראש החושב",
      srcName: "thinking_head"
    }
  };

  componentDidMount() {
    this.setState(prevState => ({
      ...prevState,
      gameWidth: window.innerWidth
    }));
  }

  changeEnemy = () => {
    this.setState(prevState => ({
      ...prevState,
      currentEnemy: {
        hp: 100,
        name: "אליעזר בן יהודה",
        srcName: "eliazer_ben_judah"
      }
    }));
  };

  render() {
    return (
      <div className="Game">
        <HUD playerHP={100} currentEnemy={this.state.currentEnemy} />
        <Foreground
          gameWidth={this.state.gameWidth}
          currentEnemy={this.state.currentEnemy}
        />
        <style jsx>{`
          .Game {
            position: absolute;
            background-image: url(/static/img/background.jpg);
            height: 100%;
            width: 100%;
            z-index: 0;
          }
        `}</style>
      </div>
    );
  }
}

export default connect()(Game);
